"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { AlertCircle, CheckCircle } from 'lucide-react'
import { Alert, AlertDescription } from "@/components/ui/alert"

export function ClaimForm() {
  const [assetId, setAssetId] = useState("")
  const [submitted, setSubmitted] = useState(false)

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (assetId) {
      setSubmitted(true)
      setTimeout(() => {
        setAssetId("")
        setSubmitted(false)
      }, 3000)
    }
  }

  return (
    <Card className="p-8 bg-card/50 backdrop-blur-sm border border-primary/10">
      <form onSubmit={handleSubmit} className="space-y-6">
        <div>
          <h2 className="text-2xl font-bold mb-6">Redeem Your Stake NFT</h2>
          <p className="text-muted-foreground mb-6">
            Enter your NFT asset ID to redeem your staked ALGO + rewards
          </p>
        </div>

        {/* Asset ID Input */}
        <div className="space-y-3">
          <Label htmlFor="assetId" className="text-base font-semibold">
            NFT Asset ID
          </Label>
          <Input
            id="assetId"
            type="number"
            placeholder="Enter your NFT asset ID"
            value={assetId}
            onChange={(e) => setAssetId(e.target.value)}
            className="h-12 text-lg"
          />
          <p className="text-sm text-muted-foreground">
            This is your unique Stake NFT identifier
          </p>
        </div>

        {/* Alert */}
        <Alert className="border border-primary/20 bg-primary/5">
          <AlertCircle className="h-4 w-4 text-primary" />
          <AlertDescription className="ml-2">
            You can only claim rewards after your lock period has ended
          </AlertDescription>
        </Alert>

        {/* Submit Button */}
        <Button
          type="submit"
          size="lg"
          className="w-full bg-primary hover:bg-primary/90 text-lg font-semibold py-6"
          disabled={!assetId}
        >
          Redeem
        </Button>

        {/* Success Message */}
        {submitted && (
          <div className="p-4 bg-secondary/10 border border-secondary/30 rounded-lg flex items-start gap-3">
            <CheckCircle className="w-5 h-5 text-secondary flex-shrink-0 mt-0.5" />
            <div>
              <p className="font-semibold text-secondary">Redemption Processed!</p>
              <p className="text-sm text-muted-foreground mt-1">
                Your ALGO and rewards have been transferred to your wallet
              </p>
            </div>
          </div>
        )}
      </form>
    </Card>
  )
}
